import React, { Component } from 'react'
import { Record } from '../Record'
import sort from '../sort'

import './style/SleepRecords.css'

const ordinal = number => {
  if (number > 3 && number < 21) {
    return String(number) + 'th'
  } else {
    const s = String(number)
    const d = s[s.length - 1]
    switch (d) {
      case '1':
        return number + 'st'
      case '2':
        return number + 'nd'
      case '3':
        return number + 'rd'
      default:
        return number + 'th'
    }
  }
}

class SleepRecord extends Component {
  constructor () {
    super()
    this.onDelete = this.onDelete.bind(this)
    this.onEditToggle = this.onEditToggle.bind(this)
    this.updateDimensions = this.updateDimensions.bind(this)
    this.onHoursChanged = this.onHoursChanged.bind(this)
    this.onMinutesChanged = this.onMinutesChanged.bind(this)
    this.state = {
      editing: false
    }
  }

  onDelete (e) {
    const date = this.props.record.date
    this.props.deleteRecord(date)
  }

  onEditToggle (e) {
    if (this.state.editing) {
      // Calculate new duration
      const hours = this.state.hours
      const minutes = this.state.minutes
      const duration = Number(hours) + (Number(minutes) / 60)
      this.props.updateRecordDuration(this.props.record.date, duration)

      // Update state
      this.setState({editing: false})
    } else {
      // Get duration from property
      const {duration} = this.props.record

      // Get hours and minutes
      const hours = Math.floor(duration)
      const minutes = Math.floor((duration % 1) * 60)

      // Update State
      this.setState({editing: true, hours, minutes})
    }
  }

  onHoursChanged (e) {
    // get value from control
    const value = e.target.value

    // Ensure positive integer
    const hours = Math.round(Math.abs(value))

    // Update state
    this.setState({hours})
  }

  onMinutesChanged (e) {
    // get value from control
    const value = e.target.value

    // Ensure positive integer
    const minutes = Math.round(Math.abs(value))

    // Update state
    this.setState({minutes})
  }

  updateDimensions () {
    this.setState({width: window.innerWidth, height: window.innerHeight})
  }
  componentWillMount () {
    this.updateDimensions()
  }
  componentDidMount () {
    window.addEventListener('resize', this.updateDimensions)
  }

  componentWillUnmount () {
    window.removeEventListener('resize', this.updateDimensions)
  }

  render () {
    // Get duration from property
    const {duration} = this.props.record

    // Get hours and minutes
    const hours = Math.floor(duration)
    const minutes = Math.round((duration % 1) * 60)

    // Create string equivalents
    const monthString = this.props.record.date.toLocaleDateString('EN-AU', { month: 'long' })
    const dayString = this.props.record.date.toLocaleDateString('EN-AU', { day: '2-digit' })

    // Convert to ordinal, i.e x -> xth
    const ordinalDayString = ordinal(+dayString)

    // Create display strings, mobile string is shorter and used on smaller screens
    const durationString = `${hours} Hours ${minutes > 0 ? `${minutes} Minutes` : ''}`
    const mobileDurationString = `${hours}${minutes > 0 ? `h ${minutes}m` : ' Hours'}`
    const useMobileStrings = this.state.width < 500
    const showDay = this.state.width > 375

    // Return the JSX element
    return (
      <div className='sleep-record'>
        <a> { showDay && this.props.record.day() } </a>
        <a> ({ monthString } { ordinalDayString }) </a>
        {
          !this.state.editing
            ? (<a> {useMobileStrings ? mobileDurationString : durationString} </a>)
            : (<a>
              <input
                className='edit-input'
                value={this.state.hours}
                onChange={this.onHoursChanged}
                type='number'
                step='1'
                min='0'
                max='24' />
              { useMobileStrings ? ':' : (<span>&nbsp; Hours &nbsp;</span>)}
              <input
                className='edit-input'
                value={this.state.minutes}
                onChange={this.onMinutesChanged}
                type='number'
                min='0'
                max='59' />
              { useMobileStrings ? '' : (<span>&nbsp; Minutes</span>)}
            </a>)
        }
        <button onClick={this.onDelete}><i className='material-icons'>delete</i></button>
        <button onClick={this.onEditToggle}><i className='material-icons'> { !this.state.editing ? 'edit' : 'done' } </i></button>
      </div>
    )
  }
}

export default class SleepRecords extends Component {
  constructor () {
    super()
    this.deleteRecord = this.deleteRecord.bind(this)
    this.updateRecordDuration = this.updateRecordDuration.bind(this)
  }

  deleteRecord (queryDate) {
    // Get records from property
    const records = this.props.getRecords()

    // Set records to a copy that has been filtered for all elements save whose
    // date is the same as the given argument
    this.props.setRecords(
      records.filter(({date}) => date.getTime() !== queryDate.getTime())
    )
  }

  updateRecordDuration (date, newDuration) {
    // Get records from property
    const records = this.props.getRecords()

    // Ensure not 0-record
    newDuration = Math.max(0.02, newDuration)

    // Set records to be existing records except where
    // a record has the query date in which case it is replaced
    // with a record that is the same except for its duration value.
    this.props.setRecords(
      records.map(record =>
        record.date === date
          ? new Record({...record, duration: newDuration})
          : record
      )
    )
  }

  render () {
    // Get records from the database
    const getRecords = this.props.getRecords
    const records = getRecords()
    const sortedRecords = sort(records, (r1, r2) => r1.date.getTime() > r2.date.getTime())
    return (
      <div className='sleep-records'>
        {
          // Convert the array of records into an array of SleepRecord React Components which will then
          // be automatically rendered in order.
          sortedRecords.length > 0
            ? sortedRecords.map((record) =>
              <SleepRecord
                key={record.date}
                record={record}
                deleteRecord={this.deleteRecord}
                updateRecordDuration={this.updateRecordDuration} />)
            : <a className='no-sleep-records'> There aren't any sleep records yet! </a>
        }
      </div>
    )
  }
}



