const ascending = (a, b) => a > b

const merge = (left, right, comparison) => {
  // Create temporary array
  let temp = []

  // Iterate through and concat smaller at each position
  while (left.length > 0 && right.length > 0) {
    temp.push(comparison(left[0], right[0]) ? right.shift() : left.shift())
  }

  // Return tempo with remaining branches concatted for cases with different sized branches
  return temp.concat(left).concat(right)
}

const sort = (array, comparison = ascending) => {
  // comparison is a function that returns whether
  // the first argument or second argument is bigger.
  // This is used to allow for the sorting of various types of non-number
  // values. It defaults to the `larger` functon defined above.
  if (array.length <= 1) {
    return array
  } else {
    // Split into half and recursively sort each branch
    const half = Math.floor(array.length / 2)
    const left = sort(array.slice(0, half))
    const right = sort(array.slice(half, array.length))

    // Merge left and right arrays inside immediately invoked function expression (IIFE)
    return merge(left, right, comparison)
  }
}

export default sort



