import React, { Component } from 'react'
import { days as daysArr, months as monthsArr } from '../dates'

import './style/Graph.css'

const fontFamily = 'Arial'
const primaryColour = 'rgb(39, 76, 150)'

const average = array =>
  array.reduce((acc, val) => acc + val, 0) / array.length

class Graph extends Component {
  constructor () {
    super()
    this.drawGraph = this.drawGraph.bind(this)
    this.updateCanvas = this.updateCanvas.bind(this)
    this.recordsUpdated = this.recordsUpdated.bind(this)
  }

  componentDidMount () {
    this.updateCanvas(this.props.getRecords())
    this.props.addListener(this.recordsUpdated)
  }

  recordsUpdated (records) {
    this.updateCanvas(records)
  }

  drawGraph (ctx, records, {width, height}) {
    const rAxisBorderSize = 20
    const tAxisBorderSize = 20
    const lAxisBorderSize = 40
    const bAxisBorderSize = 30
    const borderSizes = {
      rAxisBorderSize,
      tAxisBorderSize,
      bAxisBorderSize,
      lAxisBorderSize
    }

    // Draw axis
    this.drawBasicAxis(ctx, {width, height, ...borderSizes})
    this.drawSpecificAxis(ctx, {width, height, ...borderSizes})

    // Draw data
    const data = this.prepareData(records)
    this.drawDataPoints(ctx, data, {width, height, ...borderSizes})
  }

  drawBasicAxis (ctx, options) {
    // Get options
    const {
      width,
      height,
      rAxisBorderSize,
      tAxisBorderSize,
      bAxisBorderSize,
      lAxisBorderSize
    } = options

    // Draw axis lines
    ctx.lineWidth = 2
    ctx.beginPath()
    ctx.moveTo(width - rAxisBorderSize + 0.5 /* to meet exactly */, height - bAxisBorderSize)
    ctx.lineTo(lAxisBorderSize, height - bAxisBorderSize)
    ctx.lineTo(lAxisBorderSize, tAxisBorderSize)
    ctx.stroke()
    ctx.lineWidth = 1

    // Draw markings
    ctx.textAlign = 'center'
    ctx.textBaseline = 'top'
    ctx.fillText('24h', lAxisBorderSize / 2, tAxisBorderSize)
    ctx.fillText('18h', lAxisBorderSize / 2, tAxisBorderSize + ((height - tAxisBorderSize - bAxisBorderSize) / 4))
    ctx.fillText('12h', lAxisBorderSize / 2, tAxisBorderSize + ((height - tAxisBorderSize - bAxisBorderSize) / 2))
    ctx.fillText('6h', lAxisBorderSize / 2, tAxisBorderSize + ((height - tAxisBorderSize - bAxisBorderSize) * 3 / 4))
    ctx.textBaseline = 'bottom'
    ctx.fillText('0h', lAxisBorderSize / 2, height - bAxisBorderSize)
  }

  drawDataPoints (ctx, data, options) {
    const {
      width,
      height,
      rAxisBorderSize,
      tAxisBorderSize,
      bAxisBorderSize,
      lAxisBorderSize
    } = options
    const start = lAxisBorderSize
    const end = width - rAxisBorderSize
    const pointsWidth = end - start
    const topLine = tAxisBorderSize
    const baseLine = height - bAxisBorderSize
    const pointsHeight = baseLine - topLine
    const number = data.length
    const circRadius = 6

    // First Path (line running horizontally)
    ctx.beginPath()
    for (let i = 0; i < number; i++) {
      const value = data[i] / 24
      const xPos = start + ((pointsWidth / (number - 1)) * i)
      const yPos = baseLine - value * pointsHeight
      if (i === 0) {
        ctx.moveTo(xPos, yPos)
      } else {
        ctx.lineTo(xPos, yPos)
      }
    }
    ctx.stroke()

    // Second Pass
    for (let i = 0; i < number; i++) {
      const value = data[i] / 24
      const xPos = start + ((pointsWidth / (number - 1)) * i)
      const yPos = baseLine - value * pointsHeight

      // Vertical Lines
      if (i !== 0) {
        ctx.beginPath()
        ctx.moveTo(xPos, height - bAxisBorderSize - 1) // subtract 1 in order to meet line
        ctx.lineTo(xPos, yPos)
        ctx.strokeStyle = 'rgb(74, 104, 138)'
        ctx.stroke()
        ctx.strokeStyle = primaryColour
      }

      // Circles
      ctx.beginPath()
      ctx.arc(xPos, yPos, circRadius, 0, Math.PI * 2)
      ctx.fillStyle = primaryColour
      ctx.fill()
      ctx.beginPath()
      ctx.arc(xPos, yPos, circRadius - 1, 0, Math.PI * 2)
      ctx.fillStyle = 'rgb(140, 180, 226)'
      ctx.fill()
    }
  }

  updateCanvas (records) {
    const canvas = this.refs.canvas
    canvas.width = 2 * canvas.offsetWidth
    canvas.height = 2 * canvas.offsetHeight
    const ctx = canvas.getContext('2d')
    ctx.scale(2, 2)
    ctx.font = '15px ' + fontFamily
    ctx.fillStyle = primaryColour
    ctx.strokeStyle = primaryColour
    this.drawGraph(ctx, records, {width: canvas.width / 2, height: canvas.height / 2})
  }

  render () {
    return (
      <div className='graph'>
        <canvas ref='canvas' />
      </div>
    )
  }
}

export class WeekGraph extends Graph {
  drawSpecificAxis (ctx, options) {
    const {
      width,
      height,
      bAxisBorderSize,
      lAxisBorderSize,
      rAxisBorderSize
    } = options

    // Draw day markers
    const start = lAxisBorderSize
    const end = width - rAxisBorderSize
    const markersWidth = end - start
    const markers = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
    for (let i = 0; i < markers.length; i++) {
      const xPos = start + ((markersWidth / (markers.length - 1)) * i)
      const yPos = height - (bAxisBorderSize / 2)
      ctx.textAlign = 'center'
      ctx.textBaseline = 'middle'
      ctx.fillText(markers[i], xPos, yPos)
    }
  }

  prepareData (records) {
    const data = daysArr
      .map(day => records.filter(record => record.day() === day))
      .map(records => records.map(record => record.duration))
      .map(average)
    return data
  }
}

export class MonthGraph extends Graph {
  drawSpecificAxis (ctx, options) {
    const {
      width,
      height,
      bAxisBorderSize,
      lAxisBorderSize,
      rAxisBorderSize
    } = options

    // Draw month markers
    const start = lAxisBorderSize
    const end = width - rAxisBorderSize
    const markersWidth = end - start
    const markers = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec'
    ]
    for (let i = 0; i < markers.length; i++) {
      const xPos = start + ((markersWidth / (markers.length - 1)) * i)
      const yPos = height - (bAxisBorderSize / 2)
      ctx.textAlign = 'center'
      ctx.textBaseline = 'middle'
      if (width > 340) {
        ctx.fillText(markers[i], xPos, yPos)
      } else {
        ctx.fillText(markers[i][0], xPos, yPos)
      }
    }
  }

  prepareData (records) {
    const data = monthsArr
      .map(month => records.filter(record => record.month() === month))
      .map(records => records.map(record => record.duration))
      .map(average)
    return data
  }
}



