import React, { Component } from 'react'
import InputRange from 'react-input-range'
import { Record } from '../Record'

import './style/RecordForm.css'

const minutesToTime = x => {
  // Calculate hours and minutes
  let hours = Math.floor(x / 60) % 24
  let am = hours < 12
  let minutes = Math.round(x % 60)

  // Convert to string and ensure 2 digits
  let hoursString = String(hours % 12).padStart(2, '0')
  let minutesString = String(minutes).padStart(2, '0')

  // Correct (00:xx)pm to (12:xx)am
  if (hoursString === '00' && !am) {
    hoursString = '12'
    am = true
  }

  // Create string and return it
  let string = `${hoursString}:${minutesString}${am ? 'am' : 'pm'}`
  return string
}

export default class RecordForm extends Component {
  constructor () {
    super()

    // Bind methods to self
    this.onFormSubmit = this.onFormSubmit.bind(this)
    this.onChangeDate = this.onChangeDate.bind(this)
    this.onChangeStartTime = this.onChangeStartTime.bind(this)
    this.onChangeEndTime = this.onChangeEndTime.bind(this)
    this.resetForm = this.resetForm.bind(this)
    this.legitimateEntry = this.legitimateEntry.bind(this)
    this.recordExistsWithDate = this.recordExistsWithDate.bind(this)
    this.addRecord = this.addRecord.bind(this)

    // Initialize state
    this.state = {
      showWarning: false,
      date: '',
      sliderValue: {
        min: 22 * 60,
        max: 31 * 60
      }
    }
  }

  recordExistsWithDate (queryDate) {
    // Get records and find if a record exists with the given date
    return this.props.getRecords()
      .some(({date}) =>
        date.getTime() === queryDate.getTime()
      )
  }

  addRecord (record) {
    // Spread current records into a new array with the given record
    this.props.setRecords(
      [
        ...this.props.getRecords(),
        record
      ]
    )
  }

  legitimateEntry () {
    return (
      this.state.date &&
      this.state.date !== '' &&
      this.state.sliderValue.max &&
      this.state.sliderValue.min &&
      !this.recordExistsWithDate(new Date(this.state.date))
    )
  }

  resetForm () {
    this.setState({
      date: '',
      showWarning: false,
      sliderValue: {
        min: 22 * 60,
        max: 31 * 60
      }
    })
  }

  onFormSubmit (e) {
    // Prevent page refresh (as is default behaviour)
    e.preventDefault()

    // Reset form values
    this.resetForm()

    // get values from form
    const date = this.state.date
    const sTime = this.state.sliderValue.min
    const eTime = this.state.sliderValue.max

    // Check existence
    if (!(date && sTime && eTime)) { return }

    // Check type (must be a date)
    if (isNaN(new Date(date))) { return }

    // Check range (must go to sleep before waking)
    if (eTime < sTime) { return }

    // find minutes using regexp
    const tMinutes = eTime - sTime
    const tHours = tMinutes / 60

    // Range check (Assume user error if sleep duration is higher than 24 hours)
    if (tHours > 24) { return }

    // Create record
    const record = new Record({
      duration: tHours,
      date,
      startTime: sTime,
      endTime: eTime
    })

    // Add record
    this.addRecord(record)
  }

  onChangeDate (e) {
    this.setState({
      date: e.target.value,
      showWarning: true
    })
  }

  onChangeStartTime (e) {
    this.setState({startTime: e.target.value})
  }

  onChangeEndTime (e) {
    this.setState({endTime: e.target.value})
  }

  render () {
    return (
      <form className='record-form' onSubmit={e => this.onFormSubmit(e)}>
        <h1 className='record-form-title'> New Sleep Record </h1>
        { (!this.legitimateEntry() && this.state.showWarning) &&
          <span className='record-warning'> A record already exists with this date</span>
        }
        <div className='record-content'>
          <div className='record-labels'>
            <span className='record-label'> <span className='label-xtra'>Sleep </span>Date </span>
            <span className='record-label'> <span className='label-xtra'>Sleep </span>Duration </span>
          </div>
          <div className='record-fields'>
            <div className='record-field' id='record-date-field'>
              <input type='date' className='record-input' value={this.state.date} onChange={this.onChangeDate} />
            </div>
            <div className='record-field' id='record-start-field'>
              <InputRange
                draggableTrack
                maxValue={2460 /* 5pm */}
                minValue={720 /* 12am */}
                step={10}
                formatLabel={minutesToTime}
                onChange={sliderValue => this.setState({ sliderValue })}
                value={this.state.sliderValue}
                />
            </div>
          </div>
        </div>
        <input type='submit' disabled={!this.legitimateEntry()} className='record-submit-button' value='Create Entry' />
      </form>
    )
  }
}



