import React, { Component } from 'react'
import { download } from '../download'

export default class ExportAllButton extends Component {
  constructor () {
    super()
    this.onExportAll = this.onExportAll.bind(this)
  }

  onExportAll (e) {
    // Get records from App
    const records = this.props.getRecords()

    // Create file content
    const content = records.map(record =>
      `${record.date.toLocaleDateString()} - ${record.duration * 60} minutes`
    ).join('\n')

    // Download it!
    download('times.txt', content)
  }

  render () {
    return (
      <div className='utility-button export-all'>
        <button onClick={this.onExportAll}> Export All </button>
      </div>
    )
  }
}



