import React, { Component } from 'react'
import Header from './Header'
import { WeekGraph, MonthGraph } from './Graph'
import RecordForm from './RecordForm'
import SleepRecords from './SleepRecords'
import ExportAllButton from './ExportAllButton'
import DeleteAllButton from './DeleteAllButton'
import SleepTimeBox from './SleepTimeBox'
import {
  getRecords as getRecordsFromStorage,
  setRecords as setRecordsAtStorage
} from '../records'

import './style/App.css'
import './style/UtilityButton.css'
import 'react-input-range/lib/css/index.css'

export default class App extends Component {
  constructor () {
    super()
    this.getRecords = this.getRecords.bind(this)
    this.setRecords = this.setRecords.bind(this)
    this.addRecordChangedListener = this.addRecordChangedListener.bind(this)
    this.state = {
      records: getRecordsFromStorage(),
      recordListeners: []
    }
  }

  addRecordChangedListener (listener) {
    // Get current listeners
    let listeners = this.state.recordListeners

    // Add and update state
    listeners.push(listener)
    this.setState({recordListeners: listeners})
  }

  getRecords () {
    return this.state.records
  }

  setRecords (records) {
    // Update state
    this.setState({
      records
    })

    // Save to localStorage
    setRecordsAtStorage(records)

    // Inform listeners
    for (let listener of this.state.recordListeners) {
      listener(records)
    }
  }

  render () {
    return (
      <div className='App'>
        <Header getRecords={this.getRecords} />
        <div className='app-content'>
          <SleepTimeBox getRecords={this.getRecords} />
          <div className='app-graphs'>
            <div className='app-graph'> <WeekGraph addListener={this.addRecordChangedListener} getRecords={this.getRecords} /> </div>
            <div className='app-graph'> <MonthGraph addListener={this.addRecordChangedListener} getRecords={this.getRecords} /> </div>
          </div>
          <SleepRecords getRecords={this.getRecords} setRecords={this.setRecords} />
          <div className='app-controls'>
            <div className='app-util-box'>
              <RecordForm getRecords={this.getRecords} setRecords={this.setRecords} />
            </div>
            <div className='app-util-box'>
              <div className='app-utility'>
                <ExportAllButton getRecords={this.getRecords} />
                <DeleteAllButton getRecords={this.getRecords} setRecords={this.setRecords} />
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}



