import { Record } from './Record'

const key = 'sleep-records'

export const getRecords = () => {
  // Read JSON string from localStorage
  const string =
    window
    .localStorage
    .getItem(key)
  if (!string) { return [] }

  // Parse string into array of object 'items'
  let items = []
  try {
    items = JSON.parse(string)
    if (!items) { return [] }
  } catch (e) {
    console.warn(`Failed to parse localData JSON; "${string}"`)
  }

  // Create 'Record' objects from deserialized items
  const records = items.map(item => new Record(item))
  return records
}

export const setRecords = (records) => {
  // Make records into array of object items for easy serializiation
  const items = records.map(
    record => record.asItem()
  )

  // Write to localStorage
  window
    .localStorage
    .setItem(key, JSON.stringify(items))
}



