import React, { Component } from 'react'
import SuggestedTime from './SuggestedTime'

import './style/SleepTimeBox.css'

export default class SleepTimeBox extends Component {
  render () {
    return (
      <div className='sleep-time-box'>
        <SuggestedTime id='suggested-sleep-time-footer' getRecords={this.props.getRecords} />
      </div>
    )
  }
}



