import React, { Component } from 'react'

import './style/SuggestedTime.css'

const minutesToTime = x => {
  // Calculate hours and minutes
  let hours = Math.floor(x / 60) % 24
  let am = hours < 12
  let minutes = Math.round(x % 60)

  // Convert to string and ensure 2 digits
  let hoursString = String(hours % 12).padStart(2, '0')
  let minutesString = String(minutes).padStart(2, '0')

  // Correct (00:xx)pm to (12:xx)am
  if (hoursString === '00' && !am) {
    hoursString = '12'
    am = true
  }

  // Create string and return it
  let string = `${hoursString}:${minutesString}${am ? 'am' : 'pm'}`
  return string
}

export default class SuggestedTime extends Component {
  constructor () {
    super()
    this.prediction = this.prediction.bind(this)
  }

  prediction () {
    // Get records from localStorage
    const records = this.props.getRecords()

    // If there arent any then we cant make a prediction
    if (records.length === 0) { return }

    // Calculate average wake-up time
    const averageEndTime = records.map(({endTime}) => endTime).reduce((a, b) => a + b, 0) / records.length

    // Suggested time is the recommended sleep duration before the average wake up time
    const recommendedDuration = 9 * 60
    const recommendedStartTime = averageEndTime - recommendedDuration
    return minutesToTime(recommendedStartTime)
  }

  render () {
    return (
      <a className='suggested-sleep-time' id={this.props.id}>
        {
          this.prediction() && `Suggested Sleep Time - ${this.prediction()}`
        }
      </a>
    )
  }
}



